#PanelAcercaDeNosotros {
    text-align: center;
}

.contenido {
    display: flex;
    justify-content: space-around;
    margin: 20px;
}

.informacion, .mision-vision {
    width: 45%;
}
nav ul li a:hover {
    color: #ffcc00;
    border-bottom: 2px solid #ffcc00;
}
h3 {
    color: #333;
    font-size: 24px;
}

p {
    color: #666;
    font-size: 16px;
}
.informacion:hover, .mision-vision:hover {
    transform: scale(1.05);
    transition: transform 0.2s ease-in-out;
    cursor: pointer;
    box-shadow: 20px 20px 15px rgba(225, 226, 233, 0.74);
}
