.BarraDeIncio .imgPatito { /* Imagen del pato de la barra de búsqueda */
  height: 40px;
  width: 40px;}
.BarraDeIncio .nav-link-hover:hover { /* Poner el color a la barra de búsqueda */
  color: red !important;}
.BarraDeIncio .nav-link-hover {/* La línea roja de abajo */
  text-decoration: none;
  position: relative;}
.BarraDeIncio .nav-link-hover::after {/* La transición de la línea roja de abajo */
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 50%;
  background-color: red;
  transition: width 0.6s ease;}
.BarraDeIncio .nav-link-hover:hover::after {/* Expansión de la línea roja */

  width: 100%;
  left: 0;}