body {
  background: #7f7fd5; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.titulo2da { /*Titulo catalogo de productos */
  font-family: "Times New Roman", serif;
  text-align: center;
  transition: transform 0.5s ease-in-out, color 0.5s ease-in-out, border 0.5s ease-in-out;
  color: red;
  border: 2px solid transparent;
  border-image-source: linear-gradient(90deg, red, yellow, blue, pink, black, red);
  border-image-slice: 1;}
.titulo2da:hover {/*hoaver del catalogo de productos*/
  transform: scale(2.2);
  color: rgb(255, 166, 0);
  border-color: transparent;}
footer {/*Desde aca es footer para todos*/
  background-color: #333;
  color: #fff;
  padding: 20px;
  text-align: center;}   
footer p {
  margin: 10px 0;}
nav ul {
  list-style: none;
  padding: 5px;}
nav li {
  display: inline;
  margin-right: 15px; }
nav a {
  color: #fff;
  text-decoration: none;}   
nav a:hover {
  border-bottom: 4px solid red;}