.divgrandeCYU {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.redesSociales {
    margin-bottom: 20px;
}

.iconoRedSocial {
    font-size: 24px;
    margin-right: 10px;
    color: #333;
    transition: color 0.3s ease-in-out, font-size 0.3s ease-in-out; 
}

.iconoRedSocial:hover {
    color: #ffcc00;
    font-size: 60px;
}

.whatsapp p {
    font-size: 30px;
    color: #333;
}

.ubicacion {
    text-align: center;
}

.imagenUbicacion {
    width: 500px;
    height: 300px;
    margin-bottom: 10px;
}
