.carrusel {
  /* Carrusel de fotos */
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.imagenes {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end; /* Ajuste aquí para alinear al final */
}

.img {
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.85);
  height: 576px;
  width: 100%; /* Cambiado a 100% para adaptarse al contenedor */
  max-width: 1134px; /* Añadido para evitar que la imagen sea más grande que su contenedor */
  border-radius: 15px;
  margin: 5px;
  object-fit: cover;
  filter: saturate(175%);
}

.texto {
  overflow: hidden;
  position: absolute;
  flex-direction: column;
  transform: translateY(0px);
  margin-bottom: 9px;
  backdrop-filter: blur(20px);
  background-color: rgba(8, 225, 88, 0.21);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
  width: 100%; /* Añadido para ocupar todo el ancho del contenedor */
}

.texto h3 {
  text-shadow: 0px 0px 15px black;
  padding-top: 40px;
  color: white;
  font-weight: 300;
  font-size: 27px;
}

.texto p {
  align-items: center;
  padding: 20px;
  color: white;
  font-size: 18px; /* Ajustado el tamaño de fuente */
  font-weight: 300;
}

.imagenes .texto {
  width: 30%; /* Añadido para ocupar todo el ancho del contenedor */
  height: 65px;
  transition: 1s;
}

.imagenes:hover .texto {
  transition: height 1s, transform 1s, background-color 1s;
  transform: translateY(-150px);
  height: 300px;
  background-color: rgba(231, 186, 25, 0.71);
}
.Divgrande {
  display: flex;

  align-items: center;
  justify-content: center;
}
.informacion-descuentos {
  text-align: center;
  max-width: 600px; 
  margin-bottom: 20px;
}

.informacion-descuentos h3 {
  color: #333; 
}
.informacion-descuentos p {
  color: #666; 
}
