
.imgSupermercado {/* Imagen del supermercado al inicio */
  height: 760px;
  width: 1900px;
  margin-left: -300px;
  display: block;}
.ver-mas-boton {/* Botón en medio de la imagen de inicio "ver más" */
  position: absolute;
  top: 50%;
  left: 44%;
  transform: translate(-50%, -50%);
  display: inline-block;
  padding: 15px 70px;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
  background-color: red;
  color: #fff;
  border-radius: 10px;
  transition: background-color 0.3s ease;
  z-index: 2;}
.ver-mas-boton:hover {/* Botón "ver más" hover */
  background-color: rgb(0, 110, 255);}