.divgrandeBON {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.noticiasBON {
    text-align: center;
}
.noticiasBON h3{
    color: rgb(216, 168, 8);
    text-decoration: none; 
    font-size: 3em;
}
.columnasBON {
    display: flex;
    flex-direction: row; 
    align-items: center;
    gap: 50px; 
}
.HorarioBON,
.EventoBON {
    flex: 1;
    text-align: center;
    padding: 10px; 
    border-radius: 15px; 
    height: 700px;
    width: 600px;
}
.columnasBON p{
    font-size: 1.5em; 
    color: #333; 
    margin: 10px 0; 
}
.columnasBON p:first-child{
    font-weight: bold;
}
.EventoBON p {
    font-size: 1.5em; 
    color: #333; 
    margin: 20px 0; 
}

.EventoBON p:first-child {
    font-weight: bold; 
}
.imgcupondesc {
    padding: 20px;
    transition: transform 0.5s ease-in-out; 
}
.imgcupondesc:hover {
    transform: scale(2.2); 
}
.imgpormoauto {
    padding: 20px;
    transition: transform 0.5s ease-in-out; 
}
.imgpormoauto:hover {
    transform: scale(2.2); 
}
.blogBON h3{
    color: rgb(216, 168, 8);
    text-decoration: none; 
    font-size: 3em;
    text-align: center;
}
.imgpatitoBON {
    border-radius: 50%;
    overflow: hidden;
    border: 4px solid transparent;
    transition: border 0.3s ease-in-out;
    width: 500px; 
    height: 500px; 
    object-fit: cover; 
    float: left; 
    margin-right: 10px; 
}

.imgpatitoBON:hover {
    border: 4px solid red;
}

.descricionBON p {
    overflow: hidden;
}

.blogBON {
    clear: left; 
}
