  .DivgrandePSA {
    display: flex;
    justify-content: center; 
    align-items: center; 
  }

  .Delivery {
    background-color: rgb(100, 184, 223);
    padding: 15px;
    display: flex;
    flex-direction: column; 
    align-items: center; 
    height: 700px;
    width: 300px;
  }
  
  .sorteos {
    background-color: rgb(100, 184, 223);
    margin: 10px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center; 
    height: 700px;
    width: 300px;
  }
  .titulodelivery {
    font-size: 21px;
    font-weight: bold;
    color: #f87e7e; 
    margin-left: 50px;
  }
  
  .imgdelivery {
    width:80px; 
    max-width: 400px;
    border-radius: 5px; 
    margin-top: -50px; 
    margin-left: -200px;
    transition: transform 0.5s ease; 
  }
.imgdelivery:hover {
  transform: scale(1.1);
}
.DelyDesc p{
    font-size: 15px;
    color: #f87e7e; 
    text-align: center;
    font-weight: bold;
    margin-top: 10px;
}
.buttonBarraDesplegable {
    background-color: #4CAF50;
    color: white;
    padding: 15px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 10px;
    font-size: 18px; 
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  .buttonBarraDesplegable:hover {
    background-color: #0c42ca; 
  }
  .barraDesplegable {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    background-color: #27fbfb; 
    padding: 30px;
    border-radius: 20px;
  }
  
  .barraDesplegable p {
    font-size: 15px;
    color: #ee1515; 
    margin: 15px 5px;
  }
  
  .barraDesplegable img {
    width: 50px; 
    border-radius: 5px;
    margin-bottom: 1px;
  }

  .MensajeDely {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center; 
    text-align: center;
  }
  
  .pamensadely {
    font-size: 16px;
    color: #fb0202;
    transition: font-size 0.8s ease, color 0.8s ease;
  }

  .MensajeDely:hover .pamensadely {
    font-size: 50px;
    color: #9807d6;
    text-shadow: 5px 2px 10px rgba(239, 232, 4, 0.8); 
  }
