.contenidoRecetas {
    display: flex;
    justify-content: space-around;
}

.recetades,
.recetaconsejos,
.recectarapidayfa,
.recetatips {
    flex: 1;
    text-align: center;
    margin: 20px;
    padding: 10px; 
    border-radius: 20px; 
    box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.80);
    
}
.recetades {
    background-color: #ffd700; 
    height:  1000px;
    width: 300px;
}

.recetaconsejos {
    background-color: #87cefa;
    height:  1000px;
    width: 300px; 
}

.recectarapidayfa {
    background-color: #90ee90;
    height:  1000px;
    width: 300px;
}

.recetatips {
    background-color: #ffcccb; 
    height:  1000px;
    width: 300px;
}
.recetades h3,
.recetaconsejos h3,
.recectarapidayfa h3,
.recetatips h3 {
    color: #fcfcfc; 
    font-size: 28px; 
    margin-bottom: 20px; 
}
.botonRecetas {
    background-color: #cd1818;
    color: white; 
    padding: 20px 35px;
    border: none; 
    border-radius: 20px; 
    cursor: pointer;
    font-size: 20px;
}
.botonRecetas:hover {
    background-color: #eae70d; 
}
.listaRecetas {
    list-style-type: none; 
    padding: 15px;
    margin-top: 10px;
}

.listaRecetas li {
    margin-bottom: 15px; 
}
.recetas-container {
    display: flex;
    flex-wrap: wrap;
}

.receta {
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
    display: flex;
    margin-top: 15px;
}

.receta img {
    max-width: 200px;

    height: 130px;
    border-radius: 4px;
    margin-right: 10px;
    margin-top: 20px;
}
.receta:hover {
    box-shadow: 20px 20px 20px rgba(0, 0, 0, 0.5);
}

.receta img:hover {
    transform: scale(1.1);
}
.receta-content {
    flex: 1;
}

.receta h4 {
    font-size: 18px;
    margin-bottom: 8px;
}

.receta p {
    font-size: 14px;
    color: #555;
}
